@import url(https://fonts.googleapis.com/css?family=Archivo:400,500,600,700);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #F4F4F4;
}

html, body {
    max-width: 100%;
    overflow-x: hidden;
}
.Sidebar {
  background-color: #F0F0F0;
  height: 100%;
  position: fixed;
  top: 0;
  width: 250px;
  z-index: 1;
  float: left;
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 40px;
  padding-left: 0px;
  padding-top: 60px; /* Safari/Chrome, other WebKit */    /* Firefox, other Gecko */
  box-sizing: border-box;         /* Opera/IE 8+ */
  padding-bottom: 80px;
}

.Sidebar_Title_Playlists {
  font-weight: 600;
  padding-left: 40px;
}

.Sidebar_Playlist_Unselected {
  padding-left: 40px;
}

.Sidebar_Playlist_Selected {
  font-weight: 700;
  border-left: solid 10px #2FE48F;
  padding-left: 30px;
}

.Sidebar li {
  list-style-type: none;
  margin-bottom: 23px;
  color: #444444;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.Sidebar li:hover {
  cursor: pointer;
  color: #2FD566;
}

.Sidebar::-webkit-scrollbar {
  width: 5px;
  display: none;
  -webkit-transition: 2s;
  transition: 2s;
}

.Sidebar::-webkit-scrollbar-thumb {
  background: #888; 
  border-radius: 10px;
}

.Sidebar:hover::-webkit-scrollbar {
  display: block;
}

.AllTracks {
  padding: 20px 70px;
}

.AllTracks_TrackList {
  width: 100%;
}

.TrackList_HeaderCell:hover {
  cursor: pointer;
  color: #222;
}

.TrackList_Row:hover {
  background-color: #eee;
}

.AllTracks_TrackList table {
  border-collapse: collapse;
}

.AllTracks_TrackList th {
  text-align: left;
  padding-bottom: 10px;
  padding-top: 10px;
  font-weight: 400;
  color: #BBB;
  font-size: 13px;
  letter-spacing: 0.1em;
}

.AllTracks_TrackList td {
  padding: 12px 0px;
  padding-right: 20px;
  list-style-type: none;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  border-top: solid 1px #D8D8D8;
  border-collapse: collapse;
  max-width: 150px;
}

.SearchBar {
  border: none;
  width: 100%;
  box-sizing: border-box;
  padding: 10px 15px;
  background: transparent;
  font-size: 14px;
  border-radius: 3px;
  margin: 10px 0px;
}

.SearchBar:focus {
  outline: none;
  background-color: rgba(0, 0, 0, 0.04);
}

.PlaylistHeader_Name {
  font-size: 40px;
  font-weight: 700;
}

.PlaylistHeader_Subtitle {
  font-weight: normal;
  font-size: 15px;
}

.Bold {
  font-weight: bold;
  font-size: 12px;
}

.Spinner {
  height: 50px;
  margin-top: 20%;
}

.centered {
  text-align: center;
}
.Header {
  padding: 6px;
  width: 100%;
}

.Header_Img {
  float: right;
  height: 35px;
  border-radius: 30px;
  margin-top: 4px;
}

.Header_Name {
  margin-left: 10px;
  margin-right: 20px;
  margin-top: 12px;
  float: right;
}

.Header_Logout {
  margin-left: 10px;
  margin-right: 20px;
  margin-top: 7px;
  float: right;
  background: #95A4D6;
  border-radius: 100px;
  border: none;
  padding: 10px 20px;
  color: #fff;
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
}

.Header_Logout:hover {
  cursor: pointer;
  background: #8594C4;
}
.PlaylistHeader {
  padding: 70px;
  padding-top: 100px;
  padding-bottom: 30px;
  display: flex;
  overflow: hidden;
  text-overflow: ellipsis;
}

.PlaylistHeader_Metadata {
  display: inline-block;
  margin-left: 50px;
}

.PlaylistHeader_Title {
  font-weight: normal;
}

.PlaylistHeader_Name {
  font-size: 30px;
  font-weight: 700;
  margin: 0px;
}

.PlaylistHeader_Image {
  height: 150px;
  width: 150px;
  display: inline-block;
}

.PlaylistHeader_TrackCount {
  font-weight: normal;
}
.TrackList {
  padding: 20px 70px;
}

.TrackList tr:hover {
  background-color: #eee;
}

.TrackList table {
  border-collapse: collapse;
}

.TrackList th {
  text-align: left;
  padding-bottom: 10px;
  padding-top: 10px;
  font-weight: 400;
  color: #BBB;
  font-size: 13px;
  letter-spacing: 0.1em;
}

.Tracklist_Title {
  min-width: 200px;
  max-width: 200px;
}

.Tracklist_Artist_Album {
  min-width: 100px;
  max-width: 100px;
}

.TrackList td {
  padding: 12px 0px;
  padding-right: 20px;
  list-style-type: none;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  border-top: solid 1px #D8D8D8;
  border-collapse: collapse;
}

.Tracklist_Popularity {
  color: #BBB;
}
.Overview {
  padding: 20px 70px;
}
.FeatureBar_Outer {
  background: #D8D8D8;
  border-radius: 100px;
  height: 20px;
}

.FeatureBar_Inner {
  background: #757373;
  border-radius: 100px;
  height: 20px;
}

.FeatureBar_Inner:hover {
  background: #666;
}
.Stats {
  padding: 20px 70px;
}

.Stats_Averages_Title {
  font-size: 20px;
  letter-spacing: 0.05em;
}

.Stats_TableRow td {
  padding-right: 20px;
  padding-bottom: 20px;
  color: #AAA;
  font-size: 15px;
  letter-spacing: 0.1em;
}

.Stats_FeatureName:hover {
  color: #999;
  cursor: default;
}

.react-tooltip-lite {
  background: #222;
  color: white;
  border-radius: 5px;
  font-size: 14px;
  letter-spacing: 0.05em;
  width: 200px;
}

.react-tooltip-lite-arrow {
  border-color: #222;
}
.PlaylistMenu {
  padding-left: 70px;
  padding-right: 70px;
}

.PlaylistMenu ul {
  padding-left: 0px;
  border-bottom: 2px solid #D8D8D8;
  padding-bottom: 30px;
  min-width: 500px;
}

.PlaylistMenu_Active_Tab {
  font-weight: 700;
}

.PlaylistMenu li {
  list-style-type: none;
  display: inline-block;
  margin-right: 70px;
  letter-spacing: 0.05em;
  font-size: 18px;
}

.PlaylistMenu li:hover {
  cursor: pointer;
}

.PlaylistMenu_Active_Tab:after {
  content: "";
  display: block;
  margin: 0 auto;
  width: 30px;
  padding-top: 20px;
  border-bottom: 2px solid #2FD566;
}
.Home {
  color: #444444;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#FAFAFA), to(#F0F0F0));
  background-image: -webkit-linear-gradient(top, #FAFAFA 0%, #F0F0F0 100%);
  background-image: linear-gradient(-180deg, #FAFAFA 0%, #F0F0F0 100%);
  height: 100%;
  background-attachment: fixed;
}

.Home_Main_Content {
  margin-left: 250px;
}
.Landing {
  position: fixed;
  height: 100%;
  width: 100%;
  text-align: center;
  padding-top: 120px;
  color: #ffffff;
  background-image: -webkit-linear-gradient(206deg, #B873AC 5%, #8BB2E2 100%);
  background-image: linear-gradient(-116deg, #B873AC 5%, #8BB2E2 100%);
}

.Landing_Title_Playlist_Analyzer {
  font-size: 50px;
}

.Landing_Title_Spotify {
  font-size: 40px;
  font-weight: normal;
}

.Landing_Title_Web_App {
  font-size: 20px;
  font-weight: normal;
}

.Landing_Title_Name {
  font-weight: 600;
  color: #eee;
}

.Landing_Button_Login {
  width: 200px;
  height: 70px;
  background-color: #2DC760;
  border-radius: 100px;
  border: none;
  color: #fff;
  font-size: 16px;
  margin-top: 20px;
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
}

.Landing_Button_Login:hover {
  cursor: pointer;
  background-color: #2FD566;
}

.Landing_Button_Login:focus {
  outline: none;
}
.App {
  text-align: center;
  margin: 0px;
  padding: 0px;
  border: 0px;
  
}

* {
  font-family: 'Archivo', sans-serif;
}
